import React, { useState, useEffect } from 'react';
import axios from 'axios';
import StockChart from './StockChart';
import './App.css';

function App() {
  const [stocks, setStocks] = useState([]);
  const [realtimeData, setRealtimeData] = useState({});
  const [selectedStock, setSelectedStock] = useState('005930');

  useEffect(() => {
    axios.get('http://124.54.191.68:3000/api/stocks')
      .then(res => setStocks(res.data));

    const interval = setInterval(() => {
      stocks.forEach(stock => {
        axios.get(`http://124.54.191.68:3000/api/stocks/${stock.stock_code}/realtime`)
          .then(res => {
            setRealtimeData(prev => ({
              ...prev,
              [stock.stock_code]: res.data
            }));
          });
      });
    }, 5000);

    return () => clearInterval(interval);
  }, [stocks]);

  return (
    <div className="App">
      <h1>   </h1>
      
      <div className="chart-container">
        <h2>{selectedStock} </h2>
        <StockChart stockCode={selectedStock} />
      </div>

      <table>
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {stocks.map(stock => (
            <tr key={stock.stock_code} onClick={() => setSelectedStock(stock.stock_code)}>
              <td>{stock.stock_code}</td>
              <td>{stock.stock_name}</td>
              <td>{realtimeData[stock.stock_code]?.current_price?.toLocaleString()}</td>
              <td>{realtimeData[stock.stock_code]?.volume?.toLocaleString()}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

export default App;
